import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { SampleComponent } from './sample.component';

const routes: Routes = [
  { path: '', 
  component: SampleComponent,
  children: [
    { path: '', redirectTo: "sample1" },
    { path: 'sample1', loadChildren: () => import('./sample1/sample1.module').then(m => m.Sample1Module) },
    { path: 'sample1', loadChildren: () => import('./sample1/sample1.module').then(m => m.Sample1Module) },
    { path: 'sample2', loadChildren: () => import('./sample2/sample2.module').then(m => m.Sample2Module) },
    { path: 'sample3', loadChildren: () => import('./sample3/sample3.module').then(m => m.Sample3Module) },
    { path: 'sample4', loadChildren: () => import('./sample4/sample4.module').then(m => m.Sample4Module) }
  ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class SampleRoutingModule { }
